import { RendererCanvas } from '../../../../lib';

export class Solution extends RendererCanvas {
    N = 3
    constructor(div: HTMLDivElement) {
        super(div);

        let divMenu = document.createElement('div');
        divMenu.style.position = 'absolute';
        divMenu.style.top = '10px';
        divMenu.style.zIndex = '10';
        div.append(divMenu);

        let slider = document.createElement('input');
        slider.type = 'range';
        slider.min = '3';
        slider.max = '100';
        slider.step = '1';
        slider.value = '3';
        slider.style.width = '300px';
        slider.oninput = () => {
            this.N = +slider.value;
        };
        divMenu.append(slider);


        this.canvas.style.background = '#fff';
        this.ctx.translate(this.ctx.canvas.width * 0.5, this.ctx.canvas.height * 0.5);
        this.ctx.scale(2, 2);
        this.start()
    }
    public render(ctx: CanvasRenderingContext2D) {
        ctx.clearRect(-this.ctx.canvas.width * 0.5, -this.ctx.canvas.height * 0.5, ctx.canvas.width, ctx.canvas.height);

        const N = this.N;

        const xC = 0
        const yC = 0
        const radius = 50

        const angle = Math.PI * 2 / N
        const angleStart = Math.PI

        const color = ['#f00', '#0f0', '#00f']

        const path = new Path2D()

        for (let i = 0; i < N; ++i) {
            const radian = angleStart + (angle * i);
            const x = xC + Math.sin(radian) * (radius);
            const y = yC + Math.cos(radian) * (radius);
            if (i === 0) {
                path.moveTo(x, y)
            } else {
                path.lineTo(x, y)
            }
        }
        path.closePath()
        ctx.lineWidth = 0.5
        ctx.stroke(path)

        for (let i = 0; i < N; ++i) {
            const radian = angleStart + (angle * i);
            const x = xC + Math.sin(radian) * (radius);
            const y = yC + Math.cos(radian) * (radius);

            ctx.fillStyle = color[i]
            ctx.beginPath();
            ctx.arc(x, y, 3, 0, Math.PI * 2)
            ctx.closePath();
            ctx.fill()
        }
    }
}


